<template>
  <div class="time-date">
    <el-card>
      <ele-calendar v-loading="loading" @pick="pick" :render-content="renderContent" :data="datedef" :prop="prop"></ele-calendar>
      <el-row>
          <el-col :span="24">&nbsp;</el-col>

        <el-col v-show="selectDate">
          <el-switch
            @change="switchChange"
            v-model="value"
            inactive-text="启用状态">
          </el-switch>
          </el-col>
          <el-col :span="24">&nbsp;</el-col>
        <el-col :span="24">
          
          <el-form status-icon class="demo-ruleForm">
            
          
            <el-form-item label="设置人数:" style="margin-top: 10px;">
              <el-input-number v-model="num" :min="1" label="描述文字"></el-input-number>
            </el-form-item>
            <div class="itemBox">
              <el-badge v-for="item in addData" :value="item.Number" class="item" type="warning">
                <el-button size="small" @click="delItem">{{item.Date}}</el-button>
              </el-badge>
            </div>
            <el-form-item>
              <el-button @click="addNumber">添加</el-button>
              <el-button @click="primary" type="primary">提交</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
  const axios = require('axios')
  import eleCalendar from 'ele-calendar'
  import 'ele-calendar/dist/vue-calendar.css'
  export default {
    data() {
      return {
        loading: false,
        datedef: [], //选中样式数组 只要按他的格式放入数组中 就会被渲染
        value: new Date(),
        prop: 'Date', //对应日期字段名
        num: '',
        selectDate: '',
        addData: [],
        hospitalCookie: '',
        hospitalData:'',
        value:true,
        Enabled:''
      }
    },
    components: {
      eleCalendar
    },
    created() {
      let hospitalDatas = sessionStorage.getItem("hospitalData")
      this.hospitalData =JSON.parse(hospitalDatas)
      this.hospitalCookie = sessionStorage.getItem("hospitalCookie")
      this.loading = true
      axios({
        method: 'post',
        url: 'https://www.c65z45.cn/api/Reserve/GetHospitalDate',
        data: {
          Token: this.hospitalCookie,
          HospitalID: this.hospitalData.ID,
          IsHospital: 1
        }
      }).then(res => {
        if (res.data.IsSuccess) {
          this.datedef = res.data.Data
        } else {
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
        this.loading = false
      }).catch(error => {
        this.$message({
          showClose: true,
          message: '服务器错误',
          type: "error"
        });
        this.loading = false
      })

    },
    methods: {
      //修改状态
      switchChange(){
        if(this.value){
          this.Enabled = 1
        }else{
          this.Enabled = 0
        }
        this.loading = true
        axios({
        method: 'post',
        url: 'https://www.c65z45.cn/api/Reserve/EnabledDate',
        data: {
          Token: this.hospitalCookie,
          DateID: this.selectDateID,
          Enabled: this.Enabled
        }
      }).then(res => {
        if (res.data.IsSuccess) {
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "success"
          });
          axios({
        method: 'post',
        url: 'https://www.c65z45.cn/api/Reserve/GetHospitalDate',
        data: {
          Token: this.hospitalCookie,
          HospitalID: this.hospitalData.ID,
          IsHospital: 1
        }
      }).then(res => {
        console.log(res)
        if (res.data.IsSuccess) {
          this.datedef = res.data.Data
        } else {
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
        this.loading = false
      }).catch(error => {
        this.$message({
          showClose: true,
          message: '服务器错误',
          type: "error"
        });
        this.loading = false
      })
        } else {
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
        this.loading = false
      }).catch(error => {
        this.$message({
          showClose: true,
          message: '服务器错误',
          type: "error"
        });
        this.loading = false
      })
      },
      delItem(e) {
        for (var k = 0; k < this.addData.length; k++) {
          if (this.addData[k].date == e.path[0].innerText) {
            this.addData.splice(k, 1)
          }
        }
      },

      //提交修改
      primary() {
        this.addData = []
      },
      //添加人数
      addNumber() {
        let datas = {}
        datas["Date"] = this.selectDate
        datas["SelectNumber"] = '0'
        datas["Number"] = this.num
        if (!this.selectDate) {
          this.$message('请选择日期');
          return
        }
        for (var i = 0; i < this.addData.length; i++) {
          if (this.addData[i].Date == datas.Date) {
            this.$message('该日期已存在');
            return
          }
        }
        for (var j = 0; j < this.datedef.length; j++) {
          if (this.datedef[j].Date == datas.Date) {
            this.$message('该日期已提交');
            return
          }
        }
        this.addData.push(datas)
      },
      //选择时间
      pick(data, event, row, dom) {
        let d = new Date(data)
        this.selectDate = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
        this.selectDateID = row.value.ID
        if(row.value.Enabled){
          this.value = true
        }else{
          this.value = false
        }
      },
      renderContent(h, parmas) {
        const loop = data => {
          return data.defvalue.value.Enabled ? ( <
            div class = "flex2 selected" > {
              data.defvalue.text
            } <
            div > {
              data.defvalue.value.SelectNumber
            }
            / {data.defvalue.value.Number}< /div > <
            /div>
          ) : ( <
            div class = "flex2" > {
              data.defvalue.text
            } <
            div >
            无 <
            /div> < /
            div >
          )

        }
        return <div style = "min-height:60px;" > {
          loop(parmas)
        } < /div>
      }
    }

  }
</script>

<style scoped>
  .is-selected {
    color: #1989FA;
  }

  .itemBox {
    display: flex;
    padding-bottom: 10px;
  }

  .itemBox>.item {
    margin-left: 20px;
  }

  .itemBox>.item:first-child {
    margin: 0;
  }
</style>